.technologies {
    text-align: center;
    position: relative;

    width: 100%;
}
.tech-header {
    font-size: 25px;
    /* font-weight: bolder; */
    margin: 35px auto;
}
.about-me {
    text-align: center;
    position: relative;

    width: 100%;
    /* height: 100%; */
}
.about-me-wrapper {
    width: 90%;
    /* padding: 10px; */
    /* border:1px solid #000; */
    margin: 0 auto;

    position: relative;
}
.clearfix:after {
    content: ".";
    visibility: hidden;
    display: block;
    height: 0;
    clear: both;
}
.about-me-header {
    font-size: 25px;
    /* font-weight: bolder; */
    margin: 35px auto;
}
.about-me img {
    float: right;
    width: 25%;
    opacity: 0.5;
    -webkit-box-shadow: 2px 2px 50px -5px rgba(0, 0, 0, 0.75);
    box-shadow: 2px 2px 50px -5px rgba(53, 51, 51, 0.75);
}

.text-highlight {
    -webkit-box-shadow: 0px 0px 0px 3px #ffff00;
            box-shadow: 0px 0px 0px 3px #ffff00;
    line-height: 1.5;
    background-color: #ffff00;
    border-radius: 5px;
    margin: 0px 4px;
}
.description {
    /* float: left; */
    width: 75%;
    /* display: inline-block; */
    position: absolute;
    left: 5%;
    top: 20%;
    text-align: right;
    font-size: 10px;
    /* font-weight: bold; */
    z-index: 10;
}

.timeline-wrapper {
    width: 80%;
    margin: 0 auto;
    position: relative;
    padding: 25px 0;
}
.timeline-wrapper:before {
    content: "";
    width: 5px;
    height: 93%;
    background: #000;
    position: absolute;
}
.bar-container {
    font-size: 10px;
    /* padding: 10px 0; */
    display: block;
    border: 1px solid black;
}
.bar-container:before {
    content: "";
    width: 10px;
    height: 10px;
    border-radius: 50%;
    position: absolute;
    background: #9cd8c6;
}
/* .bar-container:hover{
    transform: scale(1.1);
} */
.bar-container:nth-child(odd) {
    margin: 25px 55% 25px 0;
}
.bar-container:nth-child(odd)::before {
    left: 48.5%;
    border: 5px solid #6986d8;
}
.bar-container:nth-child(even)::before {
    right: 46.5%;
    border: 5px solid #6986d8;
}
.bar-container:nth-child(even) {
    margin: 25px 0 25px 55%;
}
.contact-me {
    width: 90%;
    margin: 0 auto;
    text-align: center;
    position: relative;
}
.section-header {
    font-size: 25px;
    /* font-weight: bolder; */
    margin: 5% auto 15px auto;
}
.section-header + span {
    width: 25%;
    position: absolute;
    background: #fdd835;
    height: 10px;
    left: 30%;
    top: 35px;
    border-bottom: 2px solid #000;
}
.social {
    display: inline-block;
    width: 32px;
    padding: 10px;
    border-radius: 50%;
    height: 32px;
    -webkit-transition: all 0.2s ease-in-out;
    -o-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
    margin: 10px;
}
.social:hover {
    -webkit-transform: scale(1.25);
        -ms-transform: scale(1.25);
            transform: scale(1.25);
}
.social img {
    width: 25px;
    height: 25px;
}
.fb:hover {
    background-color: #4e609a;
}
.twitter:hover {
    background-color: #6eabeb;
}
.instagram:hover {
    background: -webkit-gradient(linear, left bottom, right top, from(#f3da58), color-stop(#c83a35), to(#aa44ab));
    background: -webkit-linear-gradient(bottom left, #f3da58, #c83a35, #aa44ab);
    background: -o-linear-gradient(bottom left, #f3da58, #c83a35, #aa44ab);
    background: linear-gradient(to top right, #f3da58, #c83a35, #aa44ab);
}
.github:hover {
    background-color: #5b2a77;
}
.linkedin:hover {
    background-color: #4973b0;
}
.hero-welcome-sp {
    color: #49c5b6;
    background: -webkit-linear-gradient(45deg, #23d5ab, #23a6d5, #e73c7e, #ee7752);
    background: -o-linear-gradient(45deg, #23d5ab, #23a6d5, #e73c7e, #ee7752);
    background: linear-gradient(45deg, #23d5ab, #23a6d5, #e73c7e, #ee7752);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    -webkit-animation: GradientReverse 15s ease infinite;
    animation: GradientReverse 15s ease infinite;
    background-size: 400% 400%;
}
@-webkit-keyframes Gradient {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}
@keyframes Gradient {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}

@-webkit-keyframes GradientReverse {
    0% {
        background-position: 100% 50%;
    }
    50% {
        background-position: 0% 50%;
    }
    100% {
        background-position: 100% 50%;
    }
}

@keyframes GradientReverse {
    0% {
        background-position: 100% 50%;
    }
    50% {
        background-position: 0% 50%;
    }
    100% {
        background-position: 100% 50%;
    }
}

