        /* height:100%; */
        /* padding:25%; */
    }
    .menu-items li {
        /* display: inline-block; */
        padding: 15px 10px;
        font-size: 24px;
    }
    .bar-container:nth-child(odd)::before {
        left: 49.5%;
    }
    .bar-container:nth-child(even)::before {
        right: 49%;
    }
    .bar-container {
        font-size: 25px;
    }
    .contact-me {
        width: 100%;
    }
    .section-header {
        font-size: 60px;
    }
}



